body {
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #E5E0DF;
  box-sizing: border-box;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}


.App-header {
  background-color: #FC6100;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}


form{
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  width: 40%;
  margin: 0 auto;
}

form button {
  padding: 2px;
  margin: 10px auto;
}
form label{
  margin: 0 auto;
  border-radius: 5px;
  padding: 2em;
}
form input {
  margin-top: 30px;
}

.form-error{
  position: absolute;
  color: red;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 6em;
  margin: -1% 6%;
  
}

.numbers-display-container{
  margin: 10px auto;
  max-width: 66%;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.indNum {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  border: 1px solid #FC6100;
  height: 25px;
  min-width: 25px;
  background-color: white;
  box-shadow: 0px 1px 2px 0 rgba(34, 36, 38, 0.15);
  border-radius: 5px;
  padding: 2px;
  margin: 5px 5px;
}
